.App {
  text-align: center;
  font-size: 26px;
  display: flex;
  align-items: center;
  flex-direction: column;
  padding: 0 10px;
}

h1 {
  font-family: monospace;
  margin: 10px 0;
  font-size: 28px;
}

.flip-card {
  margin: 20px;
  display: none;
  max-width: 280px;
}

.card_current {
  display: block;
}

button {
  background: #fff;
  box-shadow: 4px 5px 14px 0px #33333330;
  border-radius: 5px;
  border: none;
  padding: 10px 20px;
  font-size: 26px;
  margin: 15px 0;
}

.flip-card {
  background-color: transparent;
  width: 300px;
  perspective: 1000px; /* Remove this if you don't want the 3D effect */
}

/* This container is needed to position the front and back side */
.flip-card-inner {
  position: relative;
  width: 100%;
  height: 100%;
  min-height: 60vh;
  text-align: center;
  transition: transform 0.5s;
  transform-style: preserve-3d;
  border-radius: 10px;
  box-shadow: 4px 5px 14px 0px #33333330;
}

/* Do an horizontal flip when you move the mouse over the flip box container */
.flip-card_flip .flip-card-inner {
  transform: rotateY(180deg);
}

/* Position the front and back side */
.flip-card-front, .flip-card-back {
  position: absolute;
  width: 100%;
  height: 100%;
  -webkit-backface-visibility: hidden; /* Safari */
  backface-visibility: hidden;
}

/* Style the front side (fallback if image is missing) */
.flip-card-front, .flip-card-back {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

/* Style the back side */
.flip-card-back {
  transform: rotateY(180deg);
  font-size: 100px;
}

.flip-card-front {
  font-size: 150px;
  color: #ccc;
}

input {
  width: 100%;
  margin: 50px 0;
}

.title {
  width: 300px;
  font-size: 26px;
  text-align: center;
  margin-top: 30px;
}

.location {
  font-size: 26px;
}